import App from './layouts/App.vue'
import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.config.productionTip = false
import'./assets/css/base.css'   //相当于src
import'./assets/js/font.js'     //字体比值控制

import index from './page/index';
import mydoc from './page/mydoc.vue';
import articles from './page/articles.vue';
import login from './page/login.vue';
import detail from './page/detail.vue';
import reg from './page/reg.vue';

//引入全局过滤器
import './filters';

// 拦截器
import './plugins/axios'

/* 安装vue-router插件 */
Vue.use(VueRouter); // 注册路由
/* 定义路由页面 */
const routes = [
    // 这里 path: '/' 代表应用首页显示的内容
    { path: '/index', component: index },
    { path: '/mydoc', component: mydoc },
	{ path: '/articles', component: articles },
	{ path: '/login', component: login },
	{ path: '/detail', component: detail },
	{ path: '/reg', component: reg },
	{ path: '/', component: index },
 
];

const router = new VueRouter({
    // mode指定路由模式，默认'hash'，另一种可选的模式是'history'
    mode: 'history',
    routes
});

// Vue.prototype.router = router

/* 创建挂载点至#app元素，将内容显示在index.html页面 */
 let vm = new Vue({
    el: '#app',
    router,
    render: h => h(App),
	data:{
	  router:router,
	  bNav:false,
	  bFoot:false,
	  bLoading:true
	}
});
export default vm 

