
import Vue from 'vue'
import axios from 'axios';
import vm from '../main.js'
// 添加一个请求拦截器
axios.interceptors.request.use(function(config){
	// config 含有发出的请求的配置信息，axios(config)
	
	// 每次都自动携带token
let token = window.localStorage.getItem('mydoc')
token = token ? JSON.parse(window.localStorage.getItem('mydoc')).token : '',
config.headers={
	token:token
}

// 显示loading
vm.bloading = true;
return config  //  放出经过配置的请求
},function(error){
	// 发出了错误的请求，拦截
	return Promise.reject(error)
})

// 添加一个响应的拦截器
axios.interceptors.response.use(function(response){
	// response  ~~axios 请求后的res
	// 响应数据回来后，到达目标组件之前，做点事  res.status   res.data.err == 2
	// 检验返回数据，token过期，路由跳转login，传递当前路由地址
	let currentRoute = vm.router.currentRoute.fullPath;
	// 获取当前路由全路径，string
	if(response.data.err === 2 && !currentRoute.includes('/login')){
		vm.router.replace({
			path:'/login',
			query:{p:currentRoute}
		})
	}
	vm.bloading = false;
	return response 
},function(error){
	// 错误的响应，拦截
	return Promise.reject(error)
})

Vue.prototype.axios=axios;

export default axios;